import { Link } from 'react-router-dom';
import React, { useEffect, useState } from 'react';
import PersonIcon from '@material-ui/icons/Person';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';

import './Header.scss';
import { useAccessToken } from '../../../hooks';

type HeaderProps = {
}

export const Header: React.FC<HeaderProps> = (props) => {
    const [isFixed, setIsFixed] = useState(false);
    const { token } = useAccessToken();
    const authorized = token !== '';
    useEffect(() => {
        const ScrollHandle = function (e: Event) {
            if(window.pageYOffset >= 300) {
                setIsFixed(true);
            }  else {
                setIsFixed(false);
            }
        }

        window.addEventListener("scroll", ScrollHandle, {passive: true});

        return () => {
            window.removeEventListener("scroll", ScrollHandle);
        }
    }, []);

    const navClasses = `${(isFixed? "fixed-header" : "")}`;
    const titleClasses = `site-title ${(isFixed? "visible-title" : "")}`;

    function checkLogin() 
    {
        if(authorized)
        return <span className='logPerson'>
                <Link to="/order"><ShoppingCartIcon className="cart" fontSize='large'/></Link>
                <Link to="/"><PersonIcon className="person" fontSize='large'/></Link>
            </span>;
        
        return <span className="log"><li><Link to="/login">Вход/Регистрация</Link></li></span>;
    }

    return (
        <header>
            <div className="header-banner">
                <h1>GuitarShop</h1>
            </div>
            <div className="clear"></div>
            <nav className={navClasses}>
                <div className={titleClasses}>GuitarShop</div>
                <ul>
                    <li><Link to="/">Главная</Link></li>
                    <li><Link to="/goods">Товары</Link></li>
                    <li><Link to="/guitartypes">Категории</Link></li>
                    <li><Link to="/delivery-pay">Доставка и оплата</Link></li>
                    {checkLogin()}
                </ul>
            </nav>
        </header>
    );
}