import React, { useRef } from 'react';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';

type DialogWindowProps = {
    submitHandle: (some: string) => any
}

export const DialogWindow: React.FC<DialogWindowProps> = (props) => {
    const [open, setOpen] = React.useState(false);

    const inputRef = useRef<HTMLInputElement>(null);

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    const succesSubmit = () => {
        const address = inputRef.current?.value?.trim();
        
        if(Boolean(address) == false) return;    

        props.submitHandle(address!)
        setOpen(false);
    }



    return (
        <div>
            <Button variant="outlined" color="secondary" onClick={handleClickOpen}>
                Заказать
            </Button>
            <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
                <DialogTitle id="form-dialog-title">Адрес доставки</DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        Введите адрес доставки, куда должен быть доставлен ваш заказ.
        </DialogContentText>
                    <TextField
                        autoFocus
                        margin="dense"
                        id="name"
                        label="Адрес доставки"
                        type="email"
                        fullWidth
                        color="secondary"
                        InputProps={{inputProps: {ref: inputRef}}}
                    />
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleClose} color="secondary">
                        Отмена
                    </Button>
                    <Button onClick={succesSubmit} color="secondary" variant="contained">
                        Подтвердить
                    </Button>
                </DialogActions>
            </Dialog>
        </div>
    );
}